import React, { useEffect, useRef } from 'react';
import Chart from 'chart.js/auto';

export {}; // Satisfies isolatedModules by marking this as a module

interface ChartProps {
  subjects: string[] | undefined;
  checkboxes: boolean[][] | undefined;
}

const ChartComponent: React.FC<ChartProps> = ({ subjects, checkboxes }) => {
  const canvasRef = useRef<HTMLCanvasElement>(null);

  useEffect(() => {
    if (!canvasRef.current || !subjects || !checkboxes) return; // Exit if refs or props are invalid

    const ctx = canvasRef.current.getContext('2d');
    if (!ctx) return;

    // Filter out empty or undefined subjects and calculate completion percentages
    const validData = subjects
      .map((subject, idx) => {
        if (!subject || !checkboxes[idx]) return null;
        const checkedCount = checkboxes[idx].filter(Boolean).length || 0;
        const percentage = Math.round((checkedCount / 31) * 100);
        return { subject, percentage };
      })
      .filter((item): item is { subject: string; percentage: number } => item !== null);

    if (validData.length === 0) {
      console.warn('No valid data for chart');
      return;
    }

    // Prepare Chart.js data
    const data = {
      labels: validData.map(item => item.subject),
      datasets: [
        {
          label: 'Completion (%)',
          data: validData.map(item => item.percentage),
          backgroundColor: 'rgba(75, 192, 192, 0.2)',
          borderColor: 'rgba(75, 192, 192, 1)',
          borderWidth: 1,
        },
      ],
    };

    // Chart options
    const options = {
      scales: {
        y: {
          beginAtZero: true,
          max: 100,
          title: {
            display: true,
            text: 'Completion Percentage (%)',
          },
        },
        x: {
          title: {
            display: true,
            text: 'Subjects',
          },
        },
      },
      plugins: {
        legend: {
          display: true,
          position: 'top' as const,
        },
      },
    };

    // Create the chart
    const chart = new Chart(ctx, {
      type: 'bar',
      data,
      options,
    });

    // Cleanup on unmount
    return () => chart.destroy();
  }, [subjects, checkboxes]);

  return <canvas ref={canvasRef} />;
};

export default ChartComponent;